<template>
  <div class="user">
    <div class="user-info">
      <div>
        <van-image
          round
          width="20vw"
          height="20vw"
          :src="avatar"
        >
        </van-image>
      </div>
      <div class="user-np">
        <div>用户名：{{mobile}}</div>
        <div>可用积分：{{balance}}</div>
      </div>
    </div>

    <van-cell-group class="user-group">
      <van-cell icon="records" title="全部订单" is-link @click="onClickOrder" />
      <van-cell icon="points" title="积分明细" is-link @click="onClickPoints" />
    </van-cell-group>

    <van-cell-group>
      <van-cell
        icon="points"
        title="重置密码"
        is-link
        @click="onClickResetPassword"
      />
    </van-cell-group>
  </div>
</template>

<script>
import { comData } from "@/mix/comData";
import { Row, Col, Icon, Cell, CellGroup, Image } from "vant";
export default {
  name: "User",

  mixins: [comData],

  components: {
    [Row.name]: Row,
    [Col.name]: Col,
    [Icon.name]: Icon,
    [Cell.name]: Cell,
    [CellGroup.name]: CellGroup,
    [Image.name]: Image
  },

  data(){
    return {
      avatar:"",
      mobile:"",
      balance:0
    }
  },

  mounted(){
    this.userInfo();
    this.userpoints()
  },

  methods: {
    onClickOrder() {
      this.$router.push({
        path: "/orderList"
      });
    },

    async userInfo(){
      const query = this.$gql`
        query userInfo($uid: String!) {
          member {
            info(
              uid: $uid
            ) {
                avatar
                nickname
                loginId
                mobile
            }
          }
        }
      `;
      const result = await this.$apollo.query({
        query: query,
        variables: {
          uid: this.uid
        }
      });
      let {
        data:{
          member:{
            info:{
              avatar,
              mobile
            }
          }
        }
      } = result;
      this.mobile = mobile
      this.avatar = avatar
      console.log('user---', mobile)
   },
   async userpoints(){
      const query = this.$gql`
        query userpoints($orgId: String!, $uid: String!) {
          points {
            account(
              orgId:$orgId
              uid: $uid
            ) {
                balance
            }
          }
        }
      `;
      const result = await this.$apollo.query({
        query: query,
        variables: {
          orgId:this.orgId,
          uid: this.uid
        }
      });
      let {
        data:{
          points:{
            account:{
              balance
            }
          }
        }
      } = result;
      this.balance = balance
      console.log('user---', balance)
   },

    onClickPoints() {
      this.$router.push({
        path: "/pointsList"
      });
    },

    onClickResetPassword() {
      this.$router.push({
        path: "/reset"
      });
    }
  }
};
</script>

