  &-info {
    display: flex;
    align-items: center;
    box-sizing: border-box;
    width: 100%;
    padding: 10px 16px;
    overflow: hidden;
    color: #323233;
    font-size: 14px;
    line-height: 24px;
    background-color: #fff;
    border: 0 solid #ebedf0;
  }
  &-np{
    margin-left:10px;
  }
  &-group {
    margin-top: 15px;
    margin-bottom: 15px;
  }
}
</style>
